// Reset

html {
  box-sizing: border-box;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
//         
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}
//        (ul)
.list {
  list-style: none;
  padding: 0;
  margin: 0;
}
//       
.link {
  text-decoration: none;
  color: inherit;
}
// link decoration
a {
  text-decoration: none;
  font-style: normal;
}
li {
  list-style: none;
}
//     
img {
  display: block;
  max-width: 100%;
  height: auto;
}
//      address
address {
  font-style: normal;
}
