@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Titan+One&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body.hidden-scroll {
  overflow: hidden;
}

.sl-overlay {
  z-index: 1035;
  background: #fff;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper {
  z-index: 1040;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.sl-wrapper * {
  box-sizing: border-box;
}

.sl-wrapper button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  font-size: 28px;
}

.sl-wrapper button:hover {
  opacity: .7;
}

.sl-wrapper .sl-close {
  z-index: 10060;
  height: 44px;
  width: 44px;
  color: #000;
  margin-top: -14px;
  margin-right: -14px;
  font-family: Arial, Baskerville, monospace;
  font-size: 3rem;
  line-height: 44px;
  display: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

.sl-wrapper .sl-counter {
  z-index: 1060;
  color: #000;
  font-size: 1rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.sl-wrapper .sl-navigation {
  width: 100%;
  display: none;
}

.sl-wrapper .sl-navigation button {
  height: 44px;
  width: 22px;
  text-align: center;
  z-index: 10060;
  color: #000;
  margin-top: -22px;
  font-family: Arial, Baskerville, monospace;
  line-height: 44px;
  display: block;
  position: fixed;
  top: 50%;
}

.sl-wrapper .sl-navigation button.sl-next {
  font-size: 2rem;
  right: 5px;
}

.sl-wrapper .sl-navigation button.sl-prev {
  font-size: 2rem;
  left: 5px;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 10px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 10px;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 20px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 20px;
  }
}

.sl-wrapper.sl-dir-rtl .sl-navigation {
  direction: ltr;
}

.sl-wrapper .sl-image {
  -ms-touch-action: none;
  touch-action: none;
  z-index: 10000;
  position: fixed;
}

.sl-wrapper .sl-image img {
  width: 100%;
  height: auto;
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

.sl-wrapper .sl-image iframe {
  background: #000;
  border: 0;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

.sl-wrapper .sl-image .sl-caption {
  color: #fff;
  background: rgba(0, 0, 0, .8);
  padding: 10px;
  font-size: 1rem;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper .sl-image .sl-caption.pos-top {
  top: 0;
  bottom: auto;
}

.sl-wrapper .sl-image .sl-caption.pos-outside {
  bottom: auto;
}

.sl-wrapper .sl-image .sl-download {
  color: #000;
  z-index: 1060;
  display: none;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.sl-spinner {
  height: 40px;
  opacity: 0;
  width: 40px;
  z-index: 1007;
  -ms-animation: pulsate 1s ease-out infinite;
  border: 5px solid #333;
  border-radius: 40px;
  margin: -20px 0 0 -20px;
  animation: pulsate 1s ease-out infinite;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
}

.sl-scrollbar-measure {
  width: 50px;
  height: 50px;
  position: absolute;
  top: -9999px;
  overflow: scroll;
}

.sl-transition {
  transition: transform .2s;
}

@keyframes pulsate {
  0% {
    opacity: 0;
    transform: scale(.1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(1.2);
  }
}

:root {
  --current-color: currentColor;
  --prmary-font: "DM Sans";
  --secondary-font: "Titan One";
  --body-text-color: #000;
  --primary-text-color: #fff;
  --secondary-text-color: #907e82;
  --btn-text-color: #d41443;
  --title-text-color: #ffa5ba;
  --accent-btn-color: #ecebeb;
  --secondary-accent-color: #d41443;
  --body-bg-color: #fff;
  --bg-product-1-color: #ffb8ca;
  --bg-product-2-color: #f0d1a5;
  --bg-product-3-color: #c2e297;
  --bg-about: #fff5f6;
  --bg-footer: #fff5f6;
  --primary-bg-hero: #ffb8ca;
  --secondary-bg-hero: #ffa5ba;
  --testomonials-bg-color: #fafafa;
  --mobile-menu-bg: #e17992;
  --footer-border-color: #e1e1e1;
  --mobile-menu-border: #e18298;
  --transition-time: .3s;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --color-backdrop-overlay: rgba(0, 0, 0, .2);
  --bg-modal: #fff5f6;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

@keyframes rotateRing {
  0% {
    transform: rotate3d(0, 0, 1, 0deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}

@keyframes scaling {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.2);
  }
}

body {
  letter-spacing: .04em;
  background: var(--body-bg-color);
  color: var(--body-text-color);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.76;
}

body.modal-open, body.menu-open {
  overflow: hidden;
}

@media screen and (min-width: 1200px) {
  body.menu-open {
    overflow: auto;
  }
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

a {
  font-style: normal;
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.section, .about {
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    margin-left: auto;
    margin-right: auto;
    padding: 0 15px;
  }
}

.btn__secondary {
  cursor: pointer;
  color: var(--btn-text-color);
  background: var(--body-bg-color);
  transition: color 250ms var(--timing-function), background-color 250ms var(--timing-function);
  border-style: none;
  border-radius: 20px;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.btn__secondary:hover, .btn__secondary:focus {
  color: var(--btn-text-color);
  background: var(--accent-btn-color);
}

.button {
  cursor: pointer;
  fill: currentColor;
  transition: color 250ms var(--timing-function), background-color 250ms var(--timing-function);
  color: var(--btn-text-color);
  background: var(--body-bg-color);
  border-style: none;
  border-radius: 22px;
  align-items: center;
  padding: 11px 25px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.button:hover, .button:focus {
  color: var(--btn-text-color);
  background: var(--accent-btn-color);
}

.button--accent {
  color: var(--body-bg-color);
  background: var(--btn-text-color);
}

.button__icon-btn-arrow {
  width: 4px;
  height: 8px;
  margin-left: 12px;
}

.backdrop {
  z-index: 1500;
  width: 100%;
  height: 100%;
  background-color: var(--color-backdrop-overlay);
  transition-property: opacity, visibility;
  transition-duration: var(--transition-time);
  transition-timing-function: var(--timing-function);
  padding: 15px 15px 15px calc(50vw - 140px);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal {
  width: calc(100% - 30px);
  max-width: 280px;
  height: calc(100% - 30px);
  background-color: var(--bg-modal);
  transition-property: transform;
  transition-duration: var(--transition-time);
  transition-timing-function: var(--timing-function);
  border-radius: 20px;
  padding-top: 40px;
  padding-bottom: 20px;
  position: absolute;
  overflow: hidden;
  transform: translateX(0);
}

.backdrop.is-hidden .modal {
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 650px;
    height: 550px;
    padding-bottom: 15px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .backdrop.is-hidden .modal {
    transform: translate(100%, -50%);
  }
}

.modal__close-button {
  z-index: 1;
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-button {
  width: 14px;
  height: 14px;
  color: var(--secondary-text-color);
  transition-property: color;
  transition-duration: var(--transition-time);
  transition-timing-function: var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
}

.close-button:hover, .close-button:focus {
  color: var(--secondary-accent-color);
  background-color: rgba(0, 0, 0, 0);
}

.modals-content {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block;
  position: relative;
}

.all-modals__item {
  width: 100%;
  height: 100%;
  padding-left: 15px;
  padding-right: 15px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
